/**
 * Importaes internas necessrias para este componente.
 * - React: permite a criao e manipulao de componentes React.
 * - useState: hook que possibilita a adio de estado a componentes funcionais.
 * - useEffect: hook utilizado para executar efeitos colaterais em componentes funcionais, como aes aps renderizaes.
 * - Loading: componente responsvel pela exibio de um indicador de carregamento.
 * - Chart: importao da classe Chart do mdulo "chart.js/auto" para a renderizao de grficos.
 */
import React, { useState, useEffect } from "react";
import Loading from "./loading";
import { Chart } from "chart.js/auto";

/**
 * Importao de um componente que atuar como um elemento do DOM neste componente.
 * - Div: importao do componente Div do mdulo "../styles/dashboardPie".
 */
import { Div } from "../styles/dashboardPie";

export default function DashBoardPie({ sector }) {
  /**
   * Variveis de estado utilizadas neste componente.
   * - dataPie: estado que armazena os dados para o grfico de pizza.
   * - loading: estado que controla a exibio do indicador de carregamento.
   */
  const [dataPie, setDataPie] = useState("");
  const [loading, setLoading] = useState(true);

  /**
   * - Acionado ao inicializar o componente para buscar dados para o dashboard de pizza.
   * - Utiliza uma funo assncrona para realizar a requisio dos dados.
   * - Realiza uma requisio GET para obter os dados do dashboard de pizza.
   * - Se a resposta for 210, define o estado de loading como verdadeiro.
   * - Se a requisio for bem-sucedida, define os dados recebidos como estado e define loading como falso.
   * - Em caso de erro, imprime o erro no console.
   * - A funo de limpeza da useEffect no  necessria neste caso, pois no h nada para limpar.
   * - O useEffect  executado apenas uma vez aps a montagem do componente devido  dependncia vazia [].
   */
  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch(`getDashBoardPie/${sector}`, {
          method: "GET",
          headers: {
            Accept: "application/json",
          },
        });

        if (response.status === 210) {
          setLoading(true);
          // Faa algo aqui se a resposta for 210
        }

        const data = await response.json();
        setDataPie(data);
        setLoading(false);
        return;
      } catch (err) {
        return console.error(err);
      }
    };

    return fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  /**
   * Acionado sempre que os dados do dashboard de pizza (dataPie) so atualizados.
   * - Obtm a referncia do elemento DOM que representa o dashboard de pizza.
   * - Inicializa uma varivel local para armazenar a instncia do grfico.
   * - Verifica se existem dados vlidos para o grfico de pizza.
   * - Se existirem dados vlidos, cria um novo grfico de pizza utilizando os dados atualizados.
   * - Se um grfico anterior j existir, ele  destrudo para evitar duplicatas.
   * - Define o grfico como estado e exibe o dashboard de pizza.
   * - O efeito  executado sempre que os dados do dashboard de pizza so modificados.
   */
  useEffect(() => {
    const dash = document.getElementById("dashpie");
    let myChart = null;

    if (dataPie && dataPie.data) {
      if (myChart) {
        myChart.destroy();
      }

      myChart = new Chart(dash, {
        type: "pie",
        data: {
          labels: ["Chamados", "Chamados em Aberto", "Chamados Finalizados", "Chamados Urgentes(mais de 7 dias aberto)"],
          datasets: [
            {
              data: dataPie.data,
              backgroundColor: ["#00b4d8", "#ffd60a", "#38b000", "#d00000"],
              hoverOffset: 4,
            },
          ],
        },
      });

      dash.style.display = "block";
    }
  }, [dataPie]);

  return (
    <Div>
      {loading && <Loading />}
      <div>
        <canvas id="dashpie" className="hidden"></canvas>
      </div>
    </Div>
  );
}
